<script>
import FundChart from "./components/FundChart";
export default {
  name: "App",
  components: {
    FundChart
  },
  created() {
    this.updateInfo();
    setInterval(this.updateInfo, 15 * 1000);
    setInterval(this.updateNextStartTime, 1000);
  },
  methods: {
    changeLang(lang) {
      this.$i18n.locale = lang;
      this.langPic = `https://cdn.vuetifyjs.com/images/flags/${lang}.png`;
    },
    toInvest() {
      var invest = document.querySelector("#invest");
      this.$SmoothScroll(invest, 500, null, null, "y");
    },
    doCopy: function() {
      var self = this;
      this.$copyText(this.contractAddr).then(
        function(e) {
          self.snackbar = true;
          console.log(e);
        },
        function(e) {
          alert("Can not copy");
          console.log(e);
        }
      );
    },
    formatSeconds(value) {
      var leave1 = value % (24 * 3600 * 1000);
      var hours = Math.floor(leave1 / (3600 * 1000));
      var leave2 = leave1 % (3600 * 1000);
      var minutes = Math.floor(leave2 / (60 * 1000));
      if (minutes < 10) {
        minutes = "0" + minutes;
      }
      var leave3 = leave2 % (60 * 1000);
      var seconds = Math.round(leave3 / 1000);
      if (seconds < 10) {
        seconds = "0" + seconds;
      }
      return `${hours} : ${minutes} : ${seconds}`;
    },
    updateNextStartTime: function() {
      const now = new Date();
      const next = new Date();
      if (now.getHours() >= 20) {
        next.setDate(now.getDate() + 1);
      } else {
        next.setDate(now.getDate());
      }
      next.setHours(20);
      next.setMinutes(0);
      next.setSeconds(0);
      this.status[1].value = this.formatSeconds(next.getTime() - now.getTime());
    },
    updateInfo: function() {
      this.$http.get("/api/v1/update?" + new Date()).then(
        response => {
          const b = response.body;
          this.update.TotalInvest = b.TotalInvest;
          this.update.TotalParticipant = b.TotalParticipant;
          this.update.TotalPayout = b.TotalPayout;
          this.update.IsRoundFinished = b.IsRoundFinished;
          this.status[0].value = this.update.IsRoundFinished
            ? this.$t("status.finished")
            : this.$t("status.run");
          this.update.NextCountdown = b.NextCountdown;
          this.update.PrizePool = b.PrizePool;
          this.status[2].value = this.update.PrizePool;
          this.update.Candidate = b.Candidate;
          if (
            this.update.Candidate ===
            "0x0000000000000000000000000000000000000000"
          ) {
            this.status[3].value = this.$t("status.NoCand");
          } else {
            this.status[3].value =
              this.update.Candidate.slice(0, 5) +
              "xxxxxx" +
              this.update.Candidate.slice(-5);
          }
        },
        response => {
          console.error(response);
        }
      );
    }
  },
  computed: {
    width() {
      return (
        window.innerWidth ||
        document.documentElement.clientWidth ||
