// CommitChart.js
import { Doughnut } from 'vue-chartjs'

export default {
    extends: Doughnut,
    mounted() {
        // Overwriting base render method with actual data.
        this.renderChart({
            labels: [this.$t('distribution.profit') + '-94%', this.$t('distribution.tech') + '-3%', this.$t('distribution.promo') + '-3%'],
            datasets: [
                {
                    label: this.$t(),
                    backgroundColor: [
                        '#ff6384',
                        '#36a2eb',
                        '#cc65fe',
                    ],
                    data: [94, 3, 3],

                }
            ],
        }, {
                animation: {
                    animateScale: true
                },
                cutoutPercentage: 50
            }
        )
    }
}



